<template>
  <button
    @click="$emit('click')"
    class="w__button"
    :disabled="disabled"
    :class="[
    'w__button--'+type,
    {'is-plain': plain, 'is-disabled': disabled, 'is-round': round},
    'w__button--size-'+size
    ]"
  >
    <i v-if="icon !== ''" :class="icon"></i>
    <slot></slot>
  </button>
</template>
<script>
  export default{
    name: 'wbutton',
    props: {
      type: {
        type: String,
        default: 'default'
      },
      size: {
        type: String,
        default: 'default'
      },
      icon: {
        type: String,
        default: ''
      },
      plain: Boolean,
      disabled: Boolean,
      round: Boolean
    },
    data () {
      return {
        msg: 'button'
      }
    }
  }
</script>
<style lang="scss">
	body{
		.w__button{
			color: black;
		}
	}
	
</style>


